import React, { PureComponent } from 'react'
// import distanceInWords from 'date-fns/distance_in_words'
import Tabs from '../components/Tabs'
import { StockPage } from './Stock/Stock'
import { SupplierPage } from './Stock/Supplier'
import { TransactionPage } from './Stock/Transaction'
import { STOCK_ENHANCED_TABS } from '../graphql/mutation/local'
import { STOCK_ENHANCED_TABS as STOCK_ENHANCED_TABS_QUERY } from '../graphql/query/local'

// console.log(distanceInWords(new Date("10/13/2018"), new Date()));

class StockDashboard extends PureComponent {
  render() {
    const defaults = [
      {
        tab: 'Stock',
        container: <StockPage {...this.props} />,
      },
      {
        tab: 'Supplier',
        container: <SupplierPage {...this.props} />,
      },
      {
        tab: 'Transaction',
        container: <TransactionPage {...this.props} />,
      },
    ]
    return (
      <Tabs defaults={defaults} localStateMutation={STOCK_ENHANCED_TABS} localStateQuery={STOCK_ENHANCED_TABS_QUERY}></Tabs>
    )
  }
}

export default StockDashboard
