import Grid from '@material-ui/core/Grid'
import { createStyles, Theme, withStyles, WithStyles } from '@material-ui/core/styles'
import React from 'react'
import EnhancedTable from '../../components/Enhanced/Table/Table'
import Mutation from '../../components/Mutation'
import TableWrapper from '../../components/TableWrapper'
import { STOCK_SUPPLIER_TABLE } from '../../graphql/mutation/local'
import { DELETE_STOCK_SUPPLIERS } from '../../graphql/mutation/remote'
import { STOCK_SUPPLIER_TABLE as STOCK_SUPPLIER_TABLE_QUERY } from '../../graphql/query/local'
import { STOCK_SUPPLIERS } from '../../graphql/query/remote'
import { defaultState } from '../../graphql/resolvers'
import { Routes } from '../../Router'
import commanRender, { RENDER_VALUE } from '../../utils/commanRender'
import getContactJSX from '../../utils/getContactJSX'

const styles = (theme: Theme) => createStyles({
  root: {
    width: '100%',
  },
  fab: {
    position: 'fixed',
    width: '4rem',
    bottom: theme.spacing.unit * 2,
    right: theme.spacing.unit * 2,
  },
})

export const stockSupplierColumnData = [
  { id: 'createdAt', numeric: false, disablePadding: false, colSpan: 1, label: 'Created At' },
  { id: 'name', numeric: false, disablePadding: false, colSpan: 1, label: 'Name' },
  { id: 'contact', numeric: false, disablePadding: false, colSpan: 1, label: 'Contact' },
]

export const stockSupplierExclude = ['id', 'createdAt', 'updatedAt', '__typename']

interface IStockItem extends WithStyles<typeof styles> {
  history?: any
}

export const SupplierPage = withStyles(styles)((props: IStockItem) => {
  const { classes, history, ...restProps } = props
  const { stockSupplierTable } = defaultState
  return (
    <div>
      <Grid container={true} className={classes.root}>
        <Grid item={true} xs={12}>
          <Mutation mutation={DELETE_STOCK_SUPPLIERS}>
            {(deleteStockSuppliers: Function, { loading: dloading }: IGraphqlData) => {
              return (
                <TableWrapper
                  tableMutation={STOCK_SUPPLIER_TABLE}
                  tableQuery={STOCK_SUPPLIER_TABLE_QUERY}
                  dataQuery={STOCK_SUPPLIERS}
                  defaultState={stockSupplierTable}
                >
                  {(tableSettings: typeof stockSupplierTable, lmutate: Function, { loading, data, refetch }: IGraphqlData) => {
                    return (
                      <EnhancedTable
                        {...restProps}
                        loading={loading || dloading}
                        data={data.suppliers.edges}
                        title='Stock Suppliers/Consumers'
                        settings={tableSettings}
                        count={data.suppliers.totalCount}
                        columnData={stockSupplierColumnData}
                        exclude={stockSupplierExclude}
                        // menu={<EnhancedMenu/>}
                        options={{
                          onView: (e: any) => history.push(Routes.StockSupplier.detail({ id: e })),
                          onEdit: (e: any) => history.push(Routes.StockSupplier.update({ id: e, edit: true })),
                          onDelete: async (args: any) => {
                            console.log('calling deleteMutate function...')
                            await deleteStockSuppliers({ variables: args })
                            await lmutate({ variables: defaultState.stockSupplierTable })
                            refetch()
                          },
                          onAdd: () => history.push(Routes.StockSupplier.create()),
                        }}
                        render={commanRender({
                          renderValue: ({ key, value }: any) => {
                            if (key === 'contact') {
                              return { value: getContactJSX(value) }
                            }
                            const result = RENDER_VALUE({ key, value, skip: ['contact'] })
                            return { ...result, value: String(value).length ? result.value : '-' }
                          },
                        })}
                        mutate={async args => {
                          await lmutate({ variables: args })
                          // refetch();
                        }}>
                      </EnhancedTable>
                    )
                  }}
                </TableWrapper>
              )
            }}

          </Mutation>
        </Grid>
      </Grid>
    </div>
  )
})
