import Grid from '@material-ui/core/Grid'
import { createStyles, Theme, withStyles, WithStyles } from '@material-ui/core/styles'
import React from 'react'
import EnhancedTable from '../../components/Enhanced/Table/Table'
import Mutation from '../../components/Mutation'
import TableWrapper from '../../components/TableWrapper'
import { STOCK_ITEM_TABLE } from '../../graphql/mutation/local'
import { DELETE_STOCK_ITEMS } from '../../graphql/mutation/remote'
import { STOCK_ITEM_TABLE as STOCK_ITEM_TABLE_QUERY } from '../../graphql/query/local'
import { STOCK_ITEMS } from '../../graphql/query/remote'
import { defaultState } from '../../graphql/resolvers'
import { Routes } from '../../Router'

const styles = (theme: Theme) => createStyles({
  root: {
    width: '100%',
  },
  fab: {
    position: 'fixed',
    width: '4rem',
    bottom: theme.spacing.unit * 2,
    right: theme.spacing.unit * 2,
  },
})

export const stockItemColumnData = [
  { id: 'createdAt', numeric: false, disablePadding: false, colSpan: 1, label: 'Created At' },
  { id: 'name', numeric: false, disablePadding: false, colSpan: 1, label: 'Name' },
  { id: 'unit', numeric: false, disablePadding: false, colSpan: 1, label: 'Unit' },
  { id: 'available', numeric: false, disablePadding: false, colSpan: 1, label: 'Available' },
]

export const stockItemExclude = ['id', 'createdAt', 'updatedAt', '__typename']

interface IStockItem extends WithStyles<typeof styles> {
  history?: any
}

export const StockPage = withStyles(styles)((props: IStockItem) => {
  const { classes, history, ...restProps } = props
  const { stockItemTable } = defaultState
  return (
    <div>
      <Grid container={true} className={classes.root}>
        <Grid item={true} xs={12}>
          <Mutation mutation={DELETE_STOCK_ITEMS}>
            {(deleteStockItems: Function, { loading: dloading }: IGraphqlData) => {
              return (
                <TableWrapper
                  tableMutation={STOCK_ITEM_TABLE}
                  tableQuery={STOCK_ITEM_TABLE_QUERY}
                  dataQuery={STOCK_ITEMS}
                  defaultState={stockItemTable}
                >
                  {(tableSettings: typeof stockItemTable, lmutate: Function, { loading, data, refetch }: IGraphqlData) => {
                    return (
                      <EnhancedTable
                        {...restProps}
                        loading={loading || dloading}
                        data={data.stocks.edges}
                        title='Stock Items'
                        settings={tableSettings}
                        count={data.stocks.totalCount}
                        columnData={stockItemColumnData}
                        exclude={stockItemExclude}
                        // menu={<EnhancedMenu/>}
                        options={{
                          onView: (e: any) => history.push(Routes.Stock.detail({ id: e })),
                          onEdit: (e: any) => history.push(Routes.Stock.update({ id: e, edit: true })),
                          onDelete: async (args: any) => {
                            console.log('calling deleteMutate function...')
                            await deleteStockItems({ variables: args })
                            await lmutate({ variables: defaultState.stockItemTable })
                            refetch()
                          },
                          onAdd: () => history.push(Routes.Stock.create()),
                        }}
                        mutate={async args => {
                          await lmutate({ variables: args })
                          // refetch();
                        }}>
                      </EnhancedTable>
                    )
                  }}
                </TableWrapper>
              )
            }}

          </Mutation>
        </Grid>
      </Grid>
    </div>

  )
})
